// vantiviewpopupManger
import manager from './manager'
import context from './context'
export default {
  // mixins: [Touch],
  props: {
    // 
    value: Boolean,
    // 
    isMask: Boolean,
    // 
    maskStyle: Object,
    // classname
    maskClass: String,
    //
    closeOnClickMask: Boolean,
    zIndex: [String, Number],
  },

  data() {
    return {
      inited: this.value // value
    };
  },
  watch: {
    value(val) {
      this.inited = this.inited || this.value;
      // debugger
      this[val ? 'open' : 'close']();
    },

  },
  mounted() {
    if (this.value) {
      this.open();
    }
  },
  beforeDestroy() {
    this.close();
  },

  methods: {
    open() {
      // cover default zIndex
      if (this.zIndex !== undefined) {
        context.zIndex = this.zIndex;
      }
      this.opened = true;
      this.renderMask();
    },

    close() {
      if (!this.opened) {
        return;
      }
      this.opened = false;
      manager.close(this);
      this.$emit('input', false);
    },
    // 
    renderMask() {
      if (this.isMask) {
        manager.open(this, {
          zIndex: context.zIndex++,
          className: this.overlayClass,
          customStyle: this.overlayStyle
        });
      } else {
        // 
        manager.close(this);
      }
      this.$nextTick(() => {
        this.$el.style.zIndex = context.zIndex++;
      });
    }
  }
};



